<script>
    import {mediaManager} from "../responsiveController";
    import Page from "../components/Page.svelte";
    const maxWidth = mediaManager.breaks[3];
    let direction = mediaManager.active <= mediaManager.breaks[3] ? 'column' : 'row';
    let width = mediaManager.active < mediaManager.breaks[3] ? '90vw' : '520px';
    let marginClasses = mediaManager.active <= mediaManager.breaks[3] ? 'mt-l2 mb-l2' : 'm-l2';
    mediaManager.addEventListener('change', (e) => {
        width = mediaManager.active < mediaManager.breaks[3] ? '90vw' : '520px';
        marginClasses = mediaManager.active <= mediaManager.breaks[3] ? 'mt-l2 mb-l2' : 'm-l2';
        direction = mediaManager.active <= mediaManager.breaks[3] ? 'column' : 'row';
    });

    const pageInfo = {
        headerText: 'Our Platforms',
        text: 'Our online disaster risk reduction and preparedness platforms are designed to make the world safer.'
    }

    const platforms = [
        {
            icon: '/planetready.png',
            url: 'https://www.planetready.com'
        },
        {
            icon: '/responder_cq.png',
            url: 'http://www.respondercq.com'
        }
    ];
</script>

<Page {...pageInfo}>
    <div class="flex-row--tc flex--wrap flex--space-around" style="flex-direction: {direction}; max-width: {maxWidth}">
        {#each platforms as plat}
            <a target="_blank" href={plat.url}>
                <div class="platform-surface flex-col--cc {marginClasses}" style="width: {width};">
                        <img src={plat.icon} alt={`${plat.title} Icon`}/>
                    <div class="visit-surface br-px4 flex-col--cc">
                        <p class="font_text-primary m-n3">Visit</p>
                    </div>
                </div>
            </a>
        {/each}
    </div>
</Page>

<style>
    .platform-surface {
        background: var(--color-primary2);
        box-shadow: 0 32px 64px rgba(0, 0, 0, .5);
        height: 20rem;
    }

    img {
        width: 420px;
    }

    .font_text-primary {
        color: var(--color-primary3);
    }

    .visit-surface {
        box-shadow: 1px 1px 2px rgba(0, 0, 0, .5);
    }

    .visit-surface:hover {
        background-color: var(--color-secondary3);
    }
</style>
