<script>
    import {mediaManager} from '../responsiveController';
    import {onMount} from 'svelte';
    import SocialIcons from "./SocialIcons.svelte";

    let open = false;

    let barFlex;
    let linkFlex;
    determineDynamicStyles()

    onMount(() => {
        const body = document.getElementsByTagName('body')[0];
        body.addEventListener('click', (e) => {
            if (e.target === e.currentTarget) {
                e.stopPropagation();
            }
            open = false;
        }, true);
    });

    function determineDynamicStyles() {
        const breaks = mediaManager.breaks;
        barFlex = mediaManager.active <= breaks[3] ? "flex-col--cc" : "flex-row--cc";
        linkFlex = mediaManager.active <= breaks[2] ? "flex-col--cc" : "flex-row--cc flex--space-between";
    }

    mediaManager.addEventListener('change', () => {
        determineDynamicStyles()
    });

    const links = [
        {
            title: "Why We Exist",
            link: 'why_we_exist'
        },
        {
            title: "Capabilities",
            link: 'featured_capabilities',
        },
        {
            title: "Platforms",
            link: 'our_platforms',
        },
        {
            title: "Team",
            link: 'our_team',
        },
        {
            title: "Impact",
            link: 'our_impact',
        },
        {
            title: "Clientele",
            link: 'our_clientele',
        },
    ]
</script>

<div class="top-bar-surface sticky {barFlex} flex--space-between">
    <div class="m-l0">
        <a href='#home'><img class="pt-n3" src='./spin_logo.png' alt='SPIN Logo'></a>
    </div>

    {#if mediaManager.active >= mediaManager.breaks[3] || open}
        <div class="m-auto w-p100 pl-l3 pr-l3 {linkFlex}">
            {#each links as {link, title}}
                <a href='#{link}'>
                    <h2 class="font_header-tertiary">{title}</h2>
                </a>
            {/each}
        </div>

        <SocialIcons/>
    {/if}

    {#if mediaManager.active < mediaManager.breaks[3] && !open}
        <div on:click={() => open = true} class="button-surface absolute">
            <a href="#/login"><img class="avatar" src="./hamburger.png" alt="avatar"/></a>
        </div>
    {/if}
</div>

<style>
    .font_header-tertiary {
        color: var(--color-primary2);
    }

    .avatar {
        max-width: 36px;
    }

    .button-surface {
        top: 28px;
        right: 50px;
        z-index: 1001;
    }

    .top-bar-surface {
        top: 0;
        z-index: 9998;
        background: var(--color-primary3);
        box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
    }

    img {
        max-width: 160px;
    }
</style>
